import { capitalize } from './format';
import siteAddons from './site-addons.json';

export type SubscriptionData = {
  product_name: string;
  title: string;
  site_id?: string;
  amount: string | number;
};

export type ZuoraSubscriptionDetails = {
  id: string;
  title: string;
  product_name: string;
  domain_name?: string;
  base: boolean;
  amount: string | number;
  quantity: number;
  site_id?: string;
  site_name: string;
  site_domain?: string;
  start_date: string;
  end_date: string | null;
};

export type OrbSubscriptionBase = {
  id: string;
  amount: number;
  end_date: string | null;
  start_date: string;
  title: string;
};

export type OrbSubscriptionDomain = {
  id: string;
  invoice_number: string;
  invoice_date: string;
  due_date: string;
  amount: string;
  invoice_items: Array<{
    quantity: number;
    title: string;
  }>;
};

export type OrbSubscriptionUsage = {
  type: AccountSubscriptionType | SiteSubscriptionType;
  amount: number;
  quantity: number;
  included: number | boolean | null;
  sites: Array<{
    site_id: string;
    site_name: string;
    site_domain?: string | null;
  }>;
};

export type AccountSubscriptions = {
  baseSubscription: SubscriptionBaseInfo | null;
  accountSubscriptions: SubscriptionAccountsInfo[];
  domainSubscriptions: SubscriptionDomainInfo[];
  siteSubscriptions: SubscriptionSiteAddonsInfo[];
};

export type SubscriptionBaseInfo = {
  id: string;
  amount: string | number;
  start_date: string;
  end_date?: string | null;
  title: string;
};

export type SubscriptionAccountsInfo = {
  quantity: number;
  included: number;
  amount: number;
  type: AccountSubscriptionType | null;
};

export type SubscriptionSiteAddonsInfo = {
  type?: SiteSubscriptionType;
  amount: number;
  quantity: number;
  sites: Array<{
    site_id: string;
    site_name: string;
    site_domain?: string | null;
  }>;
};

export type SubscriptionDomainInfo = {
  id: string;
  due_date: string;
  amount: number;
  domain?: string;
};

const SUBSCRIPTIONS_ACCOUNT_USAGE = [
  'bandwidth',
  'build_minutes',
  'edge_functions',
] as const;

const SUBSCRIPTIONS_ACCOUNT_ADDONS = [
  'concurrent_builds',
  'members',
  'active_git_contributors',
] as const;

const SUBSCRIPTIONS_ACCOUNT_LEVEL = [
  ...SUBSCRIPTIONS_ACCOUNT_USAGE,
  ...SUBSCRIPTIONS_ACCOUNT_ADDONS,
] as string[];

const SUBSCRIPTIONS_SITE_USAGE = ['form_submissions', 'functions'] as const;

const SUBSCRIPTIONS_SITE_ADDONS = [
  'identity',
  'analytics',
  'large_media',
] as const;

const SUBSCRIPTIONS_SITE_LEVEL = [
  ...SUBSCRIPTIONS_SITE_USAGE,
  ...SUBSCRIPTIONS_SITE_ADDONS,
] as string[];

type AccountSubscriptionUsageType =
  (typeof SUBSCRIPTIONS_ACCOUNT_USAGE)[number];

type AccountSubscriptionAddonsType =
  (typeof SUBSCRIPTIONS_ACCOUNT_ADDONS)[number];

type AccountSubscriptionType =
  | AccountSubscriptionAddonsType
  | AccountSubscriptionUsageType;

type SiteSubscriptionUsageType = (typeof SUBSCRIPTIONS_SITE_USAGE)[number];

type SiteSubscriptionAddonsType = (typeof SUBSCRIPTIONS_SITE_ADDONS)[number];

type SiteSubscriptionType =
  | SiteSubscriptionUsageType
  | SiteSubscriptionAddonsType;

export const getSiteAddonsInfo = () => {
  return siteAddons;
};

export const humanizeSubscriptionName = (subscriptionName: string) =>
  subscriptionName.split('_').map(capitalize).join(' ');

export const isSubscriptionUsageBased = (subscriptionName: string | null) => {
  if (!subscriptionName) return false;
  const usageBased: string[] = [
    ...SUBSCRIPTIONS_ACCOUNT_USAGE,
    ...SUBSCRIPTIONS_SITE_USAGE,
  ];
  return usageBased.includes(subscriptionName);
};

export const getSubscriptionSiteUsage = (
  subscriptions: SubscriptionSiteAddonsInfo[]
): Array<{ type: string; sites: number }> =>
  SUBSCRIPTIONS_SITE_LEVEL.map((type) => ({
    type,
    sites: (subscriptions.find((sub) => sub.type === type)?.sites || []).length,
  }));

/*
 * The following helpers are for the current Zuora subscriptions.
 * Our endpoint returns a list of all subscriptions, and we need to separate them
 * and identify them.
 *
 * Once we have migrated customers to Orb, we have new endpoints and the data is
 * separated so there is no need for this file.
 */

export const transformOrbSubscriptions = ({
  base,
  domains,
  usage,
}: {
  base?: OrbSubscriptionBase;
  domains: OrbSubscriptionDomain[];
  usage: OrbSubscriptionUsage[];
}): AccountSubscriptions => {
  // The base subscription is directly compatible
  // Transform the domain subscriptions
  const domainSubscriptions = domains.map(
    (domain): SubscriptionDomainInfo => ({
      id: domain.id,
      due_date: domain.due_date,
      amount: Number(domain.amount),
      domain: domain.invoice_items[0].title,
    })
  );

  const accountSubscriptions = usage.filter((accountUsage) =>
    SUBSCRIPTIONS_ACCOUNT_LEVEL.includes(accountUsage.type)
  ) as SubscriptionAccountsInfo[];

  const siteSubscriptions = usage.filter((accountUsage) =>
    SUBSCRIPTIONS_SITE_LEVEL.includes(accountUsage.type)
  ) as SubscriptionSiteAddonsInfo[];

  return {
    baseSubscription: base || null,
    accountSubscriptions,
    siteSubscriptions,
    domainSubscriptions,
  };
};

export const transformZuoraSubscriptions = (
  subscriptions?: ZuoraSubscriptionDetails[]
): AccountSubscriptions => {
  if (!subscriptions)
    return {
      baseSubscription: null,
      accountSubscriptions: [],
      domainSubscriptions: [],
      siteSubscriptions: [],
    };
  // Transform the base subscription -- the team plan
  const accountBaseSubscription = subscriptions.find(isZuoraSubscriptionBase);
  const baseSubscription = {
    id: accountBaseSubscription?.id,
    amount: accountBaseSubscription?.amount,
    start_date: accountBaseSubscription?.start_date,
    end_date: accountBaseSubscription?.end_date,
    title: accountBaseSubscription?.title.replace(/ team plan/g, ''),
  } as SubscriptionBaseInfo;
  // Transform account-level subscriptions (extra members + concurrent builds)
  const accountSubscriptions = subscriptions
    .filter(
      (sub) =>
        isZuoraSubscriptionAccountUsage(sub) && isZuoraSubscriptionPaid(sub)
    )
    .map(
      (subscription): SubscriptionAccountsInfo => ({
        quantity: subscription.quantity,
        amount: Number(subscription.amount),
        type: getZuoraSubscriptionAccountUsageType(subscription),
        included: 0,
      })
    );
  // Transform all the add-ons subscriptions (paid add-ons + extra members + concurrent builds)
  const siteSubscriptions = subscriptions
    .filter((sub) => isZuoraSubscriptionSiteAddon(sub))
    .reduce(
      (
        siteAddonSubscriptions: SubscriptionSiteAddonsInfo[],
        subscription: ZuoraSubscriptionDetails
      ) => {
        const type = getZuoraSubscriptionSiteAddonType(subscription);
        // Not a valid type, no problem
        if (!type) return siteAddonSubscriptions;
        // The empty subscription
        const emptySubscription: SubscriptionSiteAddonsInfo = {
          type,
          amount: 0,
          quantity: 0,
          sites: [],
        };
        const existingSubscription =
          siteAddonSubscriptions.find((sub) => sub.type === type) ||
          emptySubscription;

        // Add the amount (make sure it doesn't break if it's not a number)
        const subscriptionAmount = Number(subscription.amount);
        if (!isNaN(subscriptionAmount)) {
          existingSubscription.amount += subscriptionAmount;
        }
        // Add one to the total
        existingSubscription.quantity += 1;
        // Push the site info
        existingSubscription.sites.push({
          site_id: subscription.site_id || '',
          site_name: subscription.site_name,
          site_domain: subscription.site_domain,
        });

        const newSiteSubscriptions = [
          ...siteAddonSubscriptions.filter((sub) => sub.type !== type),
          existingSubscription,
        ];
        return newSiteSubscriptions;
      },
      []
    );

  // Transform all the domain subscriptions -- We are not getting them from the API
  const domainSubscriptions = subscriptions
    .filter(isZuoraSubscriptionDomain)
    .map(
      (subscription): SubscriptionDomainInfo => ({
        id: subscription.id,
        due_date: subscription.start_date,
        amount: Number(subscription.amount),
        domain: subscription.domain_name,
      })
    );

  return {
    baseSubscription,
    accountSubscriptions,
    siteSubscriptions,
    domainSubscriptions,
  };
};

const getZuoraSubscriptionSiteAddonType = (
  subscription: SubscriptionData
): SiteSubscriptionType | null => {
  if (isZuoraSubscriptionFormsAddon(subscription)) return 'form_submissions';
  if (isZuoraSubscriptionFunctionsAddon(subscription)) return 'functions';
  if (isZuoraSubscriptionAnalyticsAddon(subscription)) return 'analytics';
  if (isZuoraSubscriptionIdentityAddon(subscription)) return 'identity';
  if (isZuoraSubscriptionLargeMediaAddon(subscription)) return 'large_media';
  // This should not happen.
  return null;
};

const isZuoraSubscriptionSiteAddon = (subscription: SubscriptionData) => {
  return (
    isZuoraSubscriptionFormsAddon(subscription) ||
    isZuoraSubscriptionFunctionsAddon(subscription) ||
    isZuoraSubscriptionAnalyticsAddon(subscription) ||
    isZuoraSubscriptionIdentityAddon(subscription) ||
    isZuoraSubscriptionLargeMediaAddon(subscription)
  );
};

const isZuoraSubscriptionFormsAddon = (subscription: SubscriptionData) =>
  subscription.product_name === 'forms';

const isZuoraSubscriptionFunctionsAddon = (subscription: SubscriptionData) =>
  subscription.product_name === 'functions';

const isZuoraSubscriptionAnalyticsAddon = (subscription: SubscriptionData) =>
  subscription.product_name === 'analytics';

const isZuoraSubscriptionIdentityAddon = (subscription: SubscriptionData) =>
  subscription.product_name === 'identity';

const isZuoraSubscriptionLargeMediaAddon = (subscription: SubscriptionData) =>
  subscription.product_name === 'large_media';

const isZuoraSubscriptionAccountUsage = (subscription: SubscriptionData) => {
  return (
    isZuoraSubscriptionExtraMembers(subscription) ||
    isZuoraSubscriptionConcurrentBuilds(subscription) ||
    isZuoraSubscriptionBuildMinutes(subscription) ||
    isZuoraSubscriptionBandwidth(subscription) ||
    isZuoraSubscriptionEdgeFunctions(subscription)
  );
};

const getZuoraSubscriptionAccountUsageType = (
  subscription: SubscriptionData
): AccountSubscriptionType | null => {
  if (isZuoraSubscriptionExtraMembers(subscription)) return 'members';
  if (isZuoraSubscriptionConcurrentBuilds(subscription))
    return 'concurrent_builds';
  if (isZuoraSubscriptionBuildMinutes(subscription)) return 'build_minutes';
  if (isZuoraSubscriptionBandwidth(subscription)) return 'bandwidth';
  if (isZuoraSubscriptionEdgeFunctions(subscription)) return 'edge_functions';
  // This should not happen.
  return null;
};

const isZuoraSubscriptionExtraMembers = (subscription: SubscriptionData) =>
  subscription.product_name === 'Extra team members';

const isZuoraSubscriptionConcurrentBuilds = (subscription: SubscriptionData) =>
  subscription.product_name === 'Concurrent Builds';

const isZuoraSubscriptionBuildMinutes = (subscription: SubscriptionData) =>
  subscription.product_name === 'build_minutes';

const isZuoraSubscriptionBandwidth = (subscription: SubscriptionData) =>
  subscription.product_name === 'bandwidth';

const isZuoraSubscriptionEdgeFunctions = (subscription: SubscriptionData) =>
  subscription.product_name === 'edge_functions';

const isZuoraSubscriptionDomain = (subscription?: SubscriptionData) =>
  !!(subscription && /domains/i.test(subscription?.product_name));

const isZuoraSubscriptionBase = (subscription?: SubscriptionData) => {
  if (!subscription) return false;
  return (
    !isZuoraSubscriptionSiteAddon(subscription) &&
    !isZuoraSubscriptionDomain(subscription) &&
    !isZuoraSubscriptionConcurrentBuilds(subscription) &&
    !isZuoraSubscriptionExtraMembers(subscription) &&
    !isZuoraSubscriptionBuildMinutes(subscription) &&
    !isZuoraSubscriptionBandwidth(subscription) &&
    !isZuoraSubscriptionEdgeFunctions(subscription)
  );
};

const isZuoraSubscriptionPaid = (subscription?: SubscriptionData) => {
  if (!subscription) return false;
  const amount = Number(subscription.amount);
  return !isNaN(amount) && amount > 0;
};
